<template>
    <main id="cycle-report" class="p-inputtext-sm">
        <cycle-report-form />

        <semen-report-table v-if="reportType == 'semen'" />
        <patient-visit-table v-else-if="reportType == 'patientVisit'" />
        <cycle-report-table v-else />
    </main>
</template>

<script>
import { onMounted, ref } from "vue";

import CycleReportForm from "@/components/cycle-report/form.vue";
import CycleReportTable from "@/components/cycle-report/table.vue";
import SemenReportTable from "@/components/cycle-report/table-semen.vue";
import PatientVisitTable from "@/components/cycle-report/table-patient-visit.vue";
import { useStore } from "vuex";
import { computed } from "vue-demi";

export default {
    components: {
        CycleReportForm,
        CycleReportTable,
        SemenReportTable,
        PatientVisitTable
    },
    setup() {
        const store = useStore();
        onMounted(() => {
            store.dispatch("provider/getProviders");
        });

        const reportType = computed(() => store.state.cycleReport.reportType);

        return { reportType };
    }
};
</script>

<style>
#cycle-report .p-card .p-card-content {
    padding: 0 0;
}

#cycle-report .p-field {
    margin-bottom: 0.5rem;
}

#cycle-report .p-card {
    height: 100%;
}

.p-calendar,
.p-multiselect {
    height: 24.5px;
}
</style>
