<template>
    <p-card>
        <template #content>
            <div class="p-fluid p-formgrid p-grid">
                <div class="p-field p-col-12 p-md-1">
                    <label for="cycle-type" class="p-mb-1">
                        Report Type
                    </label>
                    <p-dropdown
                        v-model="form.reportType"
                        :options="reportTypes"
                        optionLabel="label"
                        optionValue="value"
                        @change="updateReportType"
                    />
                </div>
                <div
                    class="p-field p-col-12 p-md-2"
                    v-if="form.reportType !== 'semen'"
                >
                    <label for="cycle-type" class="p-mb-1">
                        Cycle Type
                    </label>
                    <p-multi-select
                        v-model="form.cycleTypes"
                        :options="cycleTypesOpntions"
                        optionLabel="cycleType"
                        optionValue="cycleType"
                    />
                </div>

                <div
                    class="p-field p-col-12 p-md-1"
                    v-if="form.reportType == 'cycle'"
                >
                    <label class="p-mb-1"> Funded </label>
                    <p-dropdown
                        v-model="form.funded"
                        :options="fundedOptions"
                        optionLabel="label"
                        optionValue="value"
                    />
                </div>
                <div
                    class="p-field p-col-12 p-md-1"
                    v-if="form.reportType == 'cycle'"
                >
                    <label class="p-mb-1">
                        Cancel
                    </label>
                    <p-dropdown
                        v-model="form.cancelled"
                        :options="cancelOptions"
                        optionLabel="label"
                        optionValue="value"
                    />
                </div>

                <div
                    class="p-field p-col-12 p-md-1"
                    v-if="form.reportType == 'semen'"
                >
                    <label for="cycle-canceled" class="p-mb-1">
                        Sperm Donor
                    </label>
                    <p-dropdown
                        v-model="form.spermDonor"
                        :options="spermDonorOptions"
                        optionLabel="label"
                        optionValue="value"
                    />
                </div>

                <div
                    class="p-field p-col-12 p-md-1"
                    v-if="form.reportType !== 'patientVisit'"
                >
                    <label for="cycle-canceled" class="p-mb-1">
                        Location
                    </label>
                    <p-dropdown
                        v-model="form.siteId"
                        :options="locationOptions"
                        optionLabel="name"
                        optionValue="id"
                    />
                </div>

                <div class="p-field p-col-12 p-md-1">
                    <label class="p-mb-1">From Date</label>
                    <p-calendar
                        :showIcon="true"
                        v-model="form.from"
                        dateFormat="yy-mm-dd"
                        :class="{
                            'p-invalid': submitted && v$.from.$invalid
                        }"
                    />
                </div>
                <div class="p-field p-col-12 p-md-1">
                    <label class="p-mb-1">To Date</label>
                    <p-calendar
                        :showIcon="true"
                        v-model="form.to"
                        dateFormat="yy-mm-dd"
                        :class="{
                            'p-invalid': submitted && v$.to.$invalid
                        }"
                    />
                </div>
                <div
                    class="p-field p-col-12 p-md-1"
                    v-if="form.reportType !== 'patientVisit'"
                >
                    <label for="cycle-type" class="p-mb-1">
                        Cycle Doctor
                    </label>
                    <p-multi-select
                        v-model="form.cycleDoctor"
                        :options="cycleDcotorOptions"
                        :filter="true"
                        optionLabel="name"
                        optionValue="id"
                    />
                </div>
                <div class="p-field p-col-12 p-md-1">
                    <label for="cycle-type" class="p-mb-1">
                        Demographic Doctor
                    </label>
                    <p-multi-select
                        v-model="form.demographicDoctor"
                        :options="demographicDcotorOptions"
                        :filter="true"
                        optionLabel="name"
                        optionValue="id"
                    />
                </div>
                <div class="p-field p-col-6 p-md-1">
                    <label class="p-mb-1">-</label>
                    <p-button label="Get Report" @click="getCycleReport" />
                </div>
                <div class="p-field p-col-6 p-md-1">
                    <label class="p-mb-1">-</label>
                    <p-button
                        label="Export XLS"
                        @click="getReportExcel"
                        :loading="loading"
                    />
                </div>
            </div>

            <div
                class="p-fluid p-formgrid p-grid"
                v-if="form.reportType == 'cycle'"
            >
                <div class="p-field p-col-12 p-md-2">
                    <label for="cycle-type" class="p-mb-1">
                        Cycle Type
                    </label>
                    <p-multi-select
                        v-model="form2.cycleTypes"
                        :options="cycleTypesOpntions"
                        optionLabel="cycleType"
                        optionValue="cycleType"
                    />
                </div>

                <div class="p-field p-col-12 p-md-1">
                    <label for="cycle-canceled" class="p-mb-1">
                        Location
                    </label>
                    <p-dropdown
                        v-model="form2.siteId"
                        :options="locationOptions"
                        optionLabel="name"
                        optionValue="id"
                        :class="{
                            'p-invalid': submitted2 && v2$.siteId.$invalid
                        }"
                    />
                </div>

                <div class="p-field p-col-12 p-md-1">
                    <label class="p-mb-1">Date</label>
                    <p-calendar
                        :showIcon="true"
                        v-model="form2.date"
                        dateFormat="yy-mm-dd"
                        :class="{
                            'p-invalid': submitted2 && v2$.date.$invalid
                        }"
                    />
                </div>

                <div class="p-field p-col-6 p-md-1">
                    <label class="p-mb-1">-</label>
                    <p-button label="Get Report" @click="getCycleDailyReport" />
                </div>
                <div class="p-field p-col-6 p-md-1">
                    <label class="p-mb-1">-</label>
                    <p-button
                        label="Export XLS"
                        @click="getDailyReportExcel"
                        :loading="loading2"
                    />
                </div>

                <div
                    class="p-field p-col-12 p-md-2"
                    v-if="form.reportType == 'cycle'"
                >
                    <label for="cycle-type" class="p-mb-1">
                        Search In Comments
                    </label>
                    <p-input-text v-model="form.comments" />
                </div>

                <div
                    class="p-field p-col-12 p-md-1"
                    v-if="form.reportType == 'cycle'"
                >
                    <label class="p-mb-1"> Pregnancy </label>
                    <p-dropdown
                        v-model="form.pregnancy"
                        :showClear="true"
                        :options="pregnancyOptions"
                        optionLabel="label"
                        optionValue="value"
                    />
                </div>
            </div>

            <!-- <div
                class="p-fluid p-formgrid p-grid"
                v-if="form.reportType == 'cycle'"
            >
                <div class="p-field p-col-12 p-md-2">
                    <label for="cycle-type" class="p-mb-1">
                        Cycle Type
                    </label>
                    <p-multi-select
                        v-model="form2.cycleTypes"
                        :options="cycleTypesOpntions"
                        optionLabel="cycleType"
                        optionValue="cycleType"
                    />
                </div>

                
                <div
                    class="p-field p-col-12 p-md-1"
                    v-if="form.reportType == 'cycle'"
                >
                    <label class="p-mb-1"> Pregnancy </label>
                    <p-dropdown
                        v-model="form.pregnancy"
                        :showClear="true"
                        :options="pregnancyOptions"
                        optionLabel="label"
                        optionValue="value"
                    />
                </div>

                <div
                    class="p-field p-col-12 p-md-1"
                    v-if="form.reportType == 'cycle'"
                >
                    <label class="p-mb-1"> Pregnancy </label>
                    <p-dropdown
                        v-model="form.pregnancy"
                        :showClear="true"
                        :options="pregnancyOptions"
                        optionLabel="label"
                        optionValue="value"
                    />
                </div>

                <div class="p-field p-col-6 p-md-1">
                    <label class="p-mb-1">-</label>
                    <p-button label="Get Report" @click="getCycleDailyReport" />
                </div>
                <div class="p-field p-col-6 p-md-1">
                    <label class="p-mb-1">-</label>
                    <p-button
                        label="Export XLS"
                        @click="getDailyReportExcel"
                        :loading="loading2"
                    />
                </div>

        
            </div> -->
        </template>
    </p-card>
</template>

<script>
import { useCycleReportForm } from "@/composable/cycle-report";
import { emitter } from "@/event-bus";
import { onMounted, watch } from "vue";
import { inject, ref } from "vue";

export default {
    setup() {
        const {
            form,
            form2,
            cycleTypesOpntions,
            fundedOptions,
            reportTypes,
            spermDonorOptions,
            cancelOptions,
            locationOptions,
            cycleDcotorOptions,
            demographicDcotorOptions,
            getCycleReport,
            getCycleDailyReport,
            getAllCycleTypes,
            getLocationOption,
            submitted,
            submitted2,
            v$,
            v2$,
            getReportExcel,
            getDailyReportExcel,
            updateReportType,
            loading,
            loading2
        } = useCycleReportForm();

        const pregnancyOptions = ref([
            { label: "Positive", value: 1 },
            { label: "Negative", value: 0 }
        ]);

        const cycleDoctorChangeListener = ({ value }) => {
            emitter.emit("cycle-report-form:cyecle-doctor-change", value);
        };

        const demograhicDoctorChangeListener = ({ value }) => {
            emitter.emit("cycle-report-form:demographic-doctor-change", value);
        };

        onMounted(() => {
            getAllCycleTypes();
            getLocationOption();
        });

        return {
            pregnancyOptions,
            loading2,
            form,
            form2,
            cycleTypesOpntions,
            fundedOptions,
            reportTypes,
            spermDonorOptions,
            cancelOptions,
            locationOptions,
            cycleDcotorOptions,
            demographicDcotorOptions,
            getCycleReport,
            getCycleDailyReport,
            submitted,
            submitted2,
            v$,
            v2$,
            getReportExcel,
            getDailyReportExcel,
            updateReportType,
            loading,
            cycleDoctorChangeListener,
            demograhicDoctorChangeListener
        };
    }
};
</script>
